body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(5,53,115);
  background: linear-gradient(90deg, rgba(5,53,115,1) 0%, rgba(29,137,193,0.9475140397956058) 68%, rgba(0,212,255,1) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
    font-family: 'Anerome Stamped';
    font-style: normal;
    font-weight: normal;
    src: local('Anerome Stamped'), url(/MonsterFriends/static/media/AneromeStamped-EaLRj.99ad3295.woff) format('woff');
    }
h1{
    font-family: 'Anerome Stamped';
    font-weight: 400;
    color: #ffffff;
}
